<template>
    <div class="questionnaireFinish">
        <!-- 头部 -->
       <navigation-bar  @onLeftClick="onLeftClick" :pageName="title" >
           
       </navigation-bar>
       <!-- 问卷详情列表 -->
       <section class="list">
           <form id="question">
               <div class="questionnaireTitle">
                    <div class="leftImg">
                        <img :src="iconImg" class="iconImg">
                    </div>
                    <div class="rightCon">{{PAPER_NAME}}</div>
                </div>
                <div class="item" v-for="(item,index) in questionList" :key="index">
                
                    <div class="questionHeader" v-if="item.QUEST_TYPE_FLAG == 1 && item.SELECT_TYPE_FLAG == 1">Q{{item.SORT_NO}} : {{item.QUEST_NAME}} (单选) </div>
                    <div class="questionHeader" v-if="item.QUEST_TYPE_FLAG == 1 && item.SELECT_TYPE_FLAG == 0">Q{{item.SORT_NO}} : {{item.QUEST_NAME}} (多选) </div>
                    <div class="questionHeader" v-if="item.QUEST_TYPE_FLAG == 0">Q{{item.SORT_NO}} : {{item.QUEST_NAME}}  </div>
                    <questionnaire-item :questionnaireCon='item'>

                    </questionnaire-item>
            </div>
           </form>
       </section>
    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import QuestionnaireItem from '@/common/servers/questionnaire/QuestionnaireItem.vue';
import { getFinishQuestData } from '@/services/servers/question';
import { Dialog } from 'vant';

export default {
    name:'questionnaireFinish',
    components:{
        NavigationBar,
        QuestionnaireItem
    },
    mounted(){
        this.params = this.$route.query
        this.PAPER_NAME = this.params.PAPER_NAME
        this.init();
        console.log('.')
    },
    data(){
        return{
            title:'问卷详情',
            userInfo:{
                 "ROOM_CODE":"123355",
                 "OWNER_CODE":"2019103016101113758"
            },
            params:{},
            PAPER_NAME:'',
            list:[
                {'header':'555'},
                {'header':'666'}
            ],
           
            questionList:[],
            iconImg:require('@/assets/images/home/question/question.png'),
            
        }
    },
    methods:{
        onLeftClick(){
            this.$store.dispatch("changeActiveFlag", 1);
            this.$router.go(-1);
        },
        init(){
            let data = {
                "PUBLISH_CODE":this.params.PUBLISH_CODE || '',
                "ROOM_CODE":this.$userInfo.ROOM_CODE || '',
                "PAPER_CODE":this.params.PAPER_CODE|| '',
                "PAPER_NAME":this.params.PAPER_NAME|| '',
                "USER_CODE":this.$userInfo.OWNER_CODE 
            };
            data = {data:JSON.stringify(data)};
            getFinishQuestData(data).then(
                res =>{
                    this.questionList = res;
                },
                err =>{

                }
            )
        },
       
    }
}
</script>

<style scoped>
.questionnaireFinish{
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    background-color: #f9f9f9
}
.questionnaireFinish .list{
    /* flex: 1; */
    overflow-y: auto;
    background-color: #ffffff;
    margin: 15px 15px 0 15px;
    box-shadow: 0 0 6px 0 rgba(219,219,219,0.50);
    border-radius: 5px;
}
.questionnaireFinish .questionnaireTitle{
    width: calc(100% - 24px);
    /* height: 24px; */
    padding: 12px;
    display: flex;
}
.questionnaireFinish .leftImg{
    height: 28px;
    width: 28px;
    margin-right: 12px
}
.questionnaireFinish .leftImg img{
    display: block;
    width: 100%;
    height: 100%;
}
.questionnaireFinish .item{
    width: 100%;
    height: auto;
    
    border-top: 1px solid #f8f8f8
}
.questionnaireFinish .rightCon{
    flex: 1;
    /* height: 24px; */
    line-height: 24px;
    font-size: 14px;
    color: #333333;
    font-weight: bold;
    word-break: break-word;
}
.questionnaireFinish .questionHeader{
    width: calc(100% - 24px);
    /* height: 20px; */
    margin: 10px 12px 0 12px;
    padding-bottom: 6px;
    font-size: 14px;
}
#question{
    height: auto;
}
</style>