<template>
  <div class="questionnaireItem">
    <!-- 单选 -->
    <div v-if="questionnaireCon.questTypeFlag == 1 && questionnaireCon.selectTypeFlag == 1">
      <van-radio-group v-model="radio" disabled="">
        <van-radio checked-color="#17B7BD" :class="[item.selected?'checkedBox':'']"
                   v-for="(item,index) in questionnaireCon.questItemList" :name="item.itemCode" :key="index">
          {{item.itemName}}
        </van-radio>
      </van-radio-group>
    </div>
    <!-- 多选 -->
    <div v-if="questionnaireCon.questTypeFlag == 1 && questionnaireCon.selectTypeFlag == 0">
      <div v-if="isForbid == true">
        <van-checkbox-group v-model="result" disabled>
          <van-checkbox checked-color="#17B7BD" v-for="(item,index) in questionnaireCon.questItemList" :key='index'
                        :name="item.itemCode">{{item.itemName}}
          </van-checkbox>
        </van-checkbox-group>
      </div>

      <div v-if="isForbid == false">
        <van-checkbox-group v-model="result" disabled>
          <van-checkbox checked-color="#17B7BD" :class="[item.selected?'checkedBox':'']"
                        v-for="(item,index) in questionnaireCon.questItemList" :key='index' :name="item.itemCode">
            {{item.itemName}}
          </van-checkbox>
        </van-checkbox-group>
      </div>
    </div>


    <!-- 问答题 -->
    <div class="questionCon questionInput" v-if="questionnaireCon.questTypeFlag == 0">
      <div class="answer">{{questionnaireCon.content}}</div>
    </div>

  </div>
</template>

<script>


  export default {
    name: 'QuestionnaireItem',
    props: {
      // 问卷题目对象
      questionnaireCon: {
        type: Object,
        default: {},
      },
      // 是否禁止选择
      isForbid: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        typeFlag: 2,
        radio: '',
        result: [],
        message: ''
      }
    },
    mounted() {
      this.setInit();
    },
    methods: {
      save() {
        let data = {};
        data.message = this.message;
        data.radio = this.radio;
        data.result = this.result;
        return data
      },
      setInit() {
        console.log("【问卷】 setInit: "+JSON.stringify(this.questionnaireCon))
        if (this.questionnaireCon.questItemList && this.questionnaireCon.questItemList.length > 0) {
          if (this.questionnaireCon.questTypeFlag == 1 && this.questionnaireCon.selectTypeFlag == 1) {
            //单选
            for (let j = 0; j < this.questionnaireCon.questItemList.length; j++) {
              this.questionnaireCon.questItemList[j].selected = this.questionnaireCon.questItemList[j].selectFlag;
              if (this.questionnaireCon.questItemList[j].selected) {
                this.radio = this.questionnaireCon.questItemList[j].itemCode || '';
              }
            }
          }
          if (this.questionnaireCon.questTypeFlag == 1 && this.questionnaireCon.selectTypeFlag == 0) {
            //多选
            this.result = [];
            for (let i = 0; i < this.questionnaireCon.questItemList.length; i++) {
              this.questionnaireCon.questItemList[i].selected = this.questionnaireCon.questItemList[i].selectFlag;
              if (this.questionnaireCon.questItemList[i].selected) {
                this.result.push(this.questionnaireCon.questItemList[i].itemCode || '')
              }
            }
          }
          if (this.questionnaireCon.questTypeFlag == 0) {
            //输入框
            this.message = this.questionnaireCon.content;
          }
        }
      }
    }
  }
</script>
<style>
  .questionnaireItem {
    width: calc(100% - 24px);
    margin: 12px;
    height: auto;
  }

  .questionnaireItem .van-radio, .questionnaireItem .van-checkbox {
    height: 30px;
    line-height: 30px
  }

  .questionnaireItem .van-radio__label, .questionnaireItem .van-checkbox__label {
    font-size: 14px;
  }

  .questionnaireItem [class*=van-hairline]::after {
    outline: none;
    border: none
  }

  .questionnaireItem .van-field__control {
    background-color: #f8f8f8;
    height: 80px !important;
    color: #333333;
    -webkit-text-fill-color: #333333;
  }

  .questionnaireItem .van-cell {
    padding: 0 !important;
  }

  .questionnaireItem .questionCon {
    height: auto;

  }

  .questionnaireItem .questionInput {
    min-height: 90px;
  }

  .questionnaireItem .van-checkbox__icon--checked.van-checkbox__icon--checked .van-icon {
    color: #ffffff;
  }

  .questionnaireItem .van-checkbox__icon--checked .van-icon-success, .questionnaireItem .van-radio__icon--checked .van-icon-success {
    background-color: #17B7BD;
    border-color: #17B7BD;
    color: #FFF !important;
  }

  .checkedBox .van-checkbox__label {
    color: #333333 !important;
  }

  .checkedBox .van-radio__label {
    color: #333333 !important;
  }

  .answer {
    background-color: #f8f8f8;
    height: 80px !important;
    color: #333333;
    -webkit-text-fill-color: #333333;
    font-size: 14px;

  }
</style>